import React, { Component } from 'react';
import { connect } from 'react-redux';
import BasicInfos from './BasicInfos';
import Assessment from './Assessment';
import TableSub from './TableSub';
import { Form, Col, Row, Button, Drawer} from 'antd';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
import SuperFetch from '../../../helpers/superFetch';
import UpdateForm from '../../Register';
import actions from '../../../redux/claim/actions';
import HeaderItem from '../../HeaderItem'
class EvaluateDetailForm extends Component {

  constructor(props) {
    super(props);

    this.state = {
      claim: {},
      visible: false,
      page:this.props.match.path.split('/')[2],
      updateVisible: false,
      flag:'decline'
    }
  }
  componentWillUnmount(){
    this.props.resetClaim()
  }
  componentDidMount() {

    if (this.props.match.params){
      this.props.getClaimByParam({id:this.props.match.params.id})
      SuperFetch.get('claim/claimById',this.props.match.params)
      .then(response => {

        if(response.status === 200) {
          var flag = '';
          if (
            response.data.ClaimInfoVisits.every(visit => {
              return visit.BillingInfos.every(billing => {
                return (!billing.approved || billing.approved === 0)
              })
            })
          ) {
            flag = 'decline'
          } else if (
            response.data.ClaimInfoVisits.every(visit => {
              return visit.BillingInfos.every(billing => {
                return (billing.value === billing.approved)
              })
            })
          ) {
            flag = 'approve'
          } else if (
            response.data.ClaimInfoVisits.some(visit => {
              return visit.BillingInfos.some(billing => {
                // console.log('billing.approved',billing.approved)
                // console.log(billing.value > billing.approved)
                return (billing.value > billing.approved)
              })
            })
          ) {
              flag = 'patial'
          } else {
            flag = 'condiseration'
          }
          this.setState({claim:response.data, flag:flag});
        } else {
          notification('error', "Something wrong in Basic data" );
        }
      })
      .catch(e => console.log(e))

      //this.props.getClaimTitleById(this.props.match.params)
    } else {
      notification('error', "No soch claim" );
    }

  }


  showDrawerUpdate = () => {
    this.setState({
      updateVisible: true,
    });
  };
  onCloseUpdate = () => {
    this.setState({ updateVisible: false });
  };

  getTotal = () => {
    if (this.props.match.params){

      SuperFetch.get('claim/claimById',this.props.match.params)
      .then(response => {

        if(response.status === 200) {
          var flag = '';
          if (
            response.data.ClaimInfoVisits.every(visit => {
              return visit.BillingInfos.every(billing => {
                return (!billing.approved || billing.approved === 0)
              })
            })
          ) {
            flag = 'decline'
          } else if (
            response.data.ClaimInfoVisits.every(visit => {
              return visit.BillingInfos.every(billing => {
                return (billing.value === billing.approved)
              })
            })
          ) {
            flag = 'approve'
          } else if (
            response.data.ClaimInfoVisits.some(visit => {
              return visit.BillingInfos.some(billing => {
                // console.log('billing.approved',billing.approved)
                // console.log(billing.value > billing.approved)
                return (billing.value > billing.approved)
              })
            })
          ) {
              flag = 'patial'
          } else {
            flag = 'condiseration'
          }
          this.setState({claim:response.data, flag:flag});
        } else {
          notification('error', "Something wrong in Basic data" );
        }
      })
      .catch(e => console.log(e))

      this.props.getClaimTitleById(this.props.match.params)
    } else {
      notification('error', "No soch claim" );
    }
  };


  render() {

    var title = () => {
      switch (this.state.page) {
        case 'evaluate':
          return 'Evaluate Claim';
        case 'decision':
          return 'Decide Claim';
        default:
          break;
      }
    }
    const rowBlockstyling = {
      marginTop: '5%'
    };
    return (
      <>
        <Col span={22} offset={1} >
          <HeaderItem title={title()}/>
          <Row type="flex">
            <Button type="primary" onClick={this.showDrawerUpdate}>
              Update Claim
            </Button>
            <Drawer
              title="UPDATE CLAIM"
              placement="right"
              closable={false}
              onClose={this.onCloseUpdate}
              visible={this.state.updateVisible}
              width={'80%'}
              style={{background: '#123b78'}}
            >
              <UpdateForm history={this.props.history}/>
            </Drawer>
          </Row>
          <Row style={rowBlockstyling}>
            <BasicInfos claim={this.state.claim} params={this.props.match.params}/>
          </Row>
          <Row>
            <Assessment claim={this.state.claim} page={this.state.page} params={this.props.match.params} history={this.props.history} match={this.props.match} flag={this.state.flag}/>
          </Row>
          <Row>
            {this.state.claim.ClaimInfoVisits? this.state.claim.ClaimInfoVisits.map(visit => {
              return <TableSub
              key={visit.id} visit={visit}
              reimbusementCurrency={this.state.claim.reimbusementCurrency}
              RCExchangeRate={this.state.claim.RCExchangeRate}
              page={this.state.page}
              getTotal={this.getTotal}
              />

            }):null}

          </Row>
        </Col>
      </>
    );
  }
}


const WrappedEvaluateDetailoForm = Form.create({ name: 'BillingInfoForm' })(EvaluateDetailForm);

export default connect(
  state => {
    return {}
  },
  actions
)(WrappedEvaluateDetailoForm);
