import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Row, Col,
Form, Input, Select, DatePicker,
Checkbox, InputNumber, Button, Icon, Table } from 'antd';
import jwtDecode from 'jwt-decode';
import action from '../../redux/claim/actions';
import currencyData from './currencyData';
import CollectionCreateForm from './collectionCreateForm';
import moment from 'moment';
const { Option } = Select;




const SvgButtonComponent = props => (
  <svg width={142.415} height={52}>
    <path
      d="M3.494 0h115.811l23.11 27.246L119.305 52H3.494C1.564 52 0 48.866 0 45V7c0-3.866 1.564-7 3.494-7z"
      fill="#f17d27"
    />
    <text
      transform="translate(61.415 33)"
      fill="#fff"
      fontSize={20}
      fontFamily="SegoeUI-Semibold, Segoe UI"
      fontWeight={600}
      letterSpacing=".02em"
    >
      <tspan x={-37.191} y={0}>
        {'SUBMIT'}
      </tspan>
    </text>
  </svg>
)

class PreviewWithLayout extends Component {
  constructor(props) {
    super(props);
    //console.log('constructor',props)
    this.state = {
      visible: false,
      visit:null,
      claim:{}
    }
  }



  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll(async (err, values) => {
      if (!err) {

        const profile = jwtDecode(this.props.idToken);
        values.createdBy = profile.id
        values.status = 'pr'

        //values

        if(this.props.page==='register') {
          this.props.initClaim({values,history:this.props.history})
        } else {
          if (this.props.claim.claimID)
            values.id = this.props.claim.claimID
          this.props.updateClaim(values);
        }

      }
    })
  };

  showModal = () => {
    this.setState({ visible: true,visit:null });
  };

  handleCancel = () => {
    this.setState({ visible: false });
  };

  handleCreate = () => {

    const { form } = this.formRef.props;
    //console.log('handleCreate',this.state.visit)
    form.validateFields((err, values) => {
      if (err) {
        return;
      }

      //format ClaimInfoVisitsFiles for db
      if(Array.isArray(values.ClaimInfoVisitsFiles)) {
        values.ClaimInfoVisitsFiles  = values.ClaimInfoVisitsFiles.map(file => {
          file.response.url = file.response.path
          return file.response
        })
      }

      //console.log('this.props.claim',this.props.claim)
      values.claimInfoId = this.props.claim.claimID
      if(this.state.visit) {
        values.id = this.state.visit.id
        this.props.updateVisit(values)


      } else {
        this.props.createVisit(values)
      }
      this.setState({ visible: false });


    });
  };

  saveFormRef = formRef => {
    this.formRef = formRef;
  };
  render() {
    // console.log('state',this.state)
    const { getFieldDecorator } = this.props.form;
    const formItemstyling = {
      marginRight: '25px'
    };
    const formItemFirstColstyling = {
      md: { span: 10 },
      xs: { span: 22 }
    };
    const formItemSecondColstyling = {
      md: { span: 10 },
      xs: { span: 22 }
    };
    const formItemThirdColstyling = {
      md: { span: 4 },
      xs: { span: 22 }
    };
    const rowBlockstyling = {
      marginBottom: '5%'
    };
    const labelstyling = {
      color:'white'
    }

    const columns = [
      {
        title: 'Date of Admission/Visit',
        dataIndex: 'dateOfAdmissionVisit',
        key: 'dateOfAdmissionVisit',
      },
      {
        title: 'Hospital or Clinic Name',
        dataIndex: 'hospitalOrClinicName',
        key: 'hospitalOrClinicName',
      },
      {
        title: 'Medical Diagnosis',
        key: 'MedicalDiagnosis',
        dataIndex: 'MedicalDiagnosis',
      },
      {
        title: 'Billing Currency',
        key: 'billingCurrency',
        dataIndex: 'billingCurrency',
      },
      {
        title: 'Hospitl or Clinic Country',
        key: 'hospitalOrClinicCountry',
        dataIndex: 'hospitalOrClinicCountry',
      },
      {
        title: 'Action',
        key: 'action',
        render: (text, visit) => {
          return (
            <div className="isoInvoiceBtnView">
              <Button
                icon="eye"
                // icon="delete"
                onClick={() => {
                  this.setState({ visible: true, visit:visit }, () => {
                    this.child.setVisit() // do stuff
                  });

                }}
              >
              </Button>
              <Button
                icon="delete"
                onClick={() => {
                  this.props.deleteVisit({id:visit.id})

                }}
              >
              </Button>
            </div>
          )
        }
      },
    ];
    return (
      <Col md={24} sm={18} xs={18}>
        <Form onSubmit={this.handleSubmit}>

          <Row name = "policyTitle">
            <svg xmlns="http://www.w3.org/2000/svg" width="218" height="40" viewBox="0 0 218 40">
              <text id="_1._Policy_Details" transform="translate(0 32)" fill="#fff" fontSize="30" fontFamily="SegoeUI-Semibold, Segoe UI" fontWeight="600"><tspan x="0" y="0">1. Policy Details</tspan></text>
            </svg>
          </Row>
          <Row name = "policyholderForm" style={rowBlockstyling}>
            <Col {...formItemFirstColstyling}>
              <Form.Item colon={false}style={formItemstyling} label={<span style={labelstyling}>Policyholder First Name:</span>}>
                {getFieldDecorator('policyFirstName', {
                  rules: [
                    {
                      required: true,
                      message: 'Please input Policyholder First Name',
                    },
                  ],
                })(
                  <Input
                    placeholder="Daniel"
                  />,
                )}
              </Form.Item>
              <Form.Item colon={false} style={formItemstyling} label={<span style={labelstyling}>Policyholder Last Name:</span>}>
                {getFieldDecorator('policyLastName', {
                  rules: [
                    {
                      required: true,
                      message: 'Please input Policyholder Last Name',
                    },
                  ],
                })(
                  <Input
                    placeholder="Smith"
                  />,
                )}
              </Form.Item>
              <Form.Item colon={false}style={formItemstyling} label={<span style={labelstyling}>Policyholder Email:</span>}>
                {getFieldDecorator('policyEmail', {
                  rules: [],
                })(
                  <Input
                  />,
                )}
              </Form.Item>
            </Col>
            <Col {...formItemSecondColstyling}>
              <Form.Item colon={false}style={formItemstyling}
              label={<span style={labelstyling}>Policy Number:</span>}>
                {getFieldDecorator('policyNumber', {
                  rules: [
                    {
                      required: true,
                      message: 'Please input Policy Number',
                    },
                  ],
                })(
                  <Input.Search placeholder="RIH/YYYY/XX/11111111" onSearch={value => {
                    if (['TLS', 'LI', 'MM', 'ST', 'CO', 'FC'].includes(value.split("/")[2])) {
                      this.props.form.setFieldsValue({policyType:value.split("/")[2]})
                    }
                  }} enterButton />,
                )}
              </Form.Item>
              <Form.Item colon={false}style={formItemstyling} label={<span style={labelstyling}>Policyholder Type:</span>}>
                {getFieldDecorator('policyType', {
                  rules: [],
                })(
                  <Select style={{ width: '100%' }} placeholder='Auto populate or select...'>
                    <Option value="TLS">Thailand Long Stay</Option>
                    <Option value="LI">Life Insurance</Option>
                    <Option value="MM">Major Medical</Option>
                    <Option value="ST">Standard</Option>
                    <Option value="CO">Comprehensive</Option>
                    <Option value="FC">Fully Comprehensive</Option>
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col {...formItemThirdColstyling}>
              <Form.Item colon={false}style={formItemstyling}
              label={<span style={labelstyling}>VIP:</span>}>
                {getFieldDecorator('policyVip', {
                  initialValue: false ,
                  valuePropName: 'checked'
                  })(
                  <Checkbox>
                  </Checkbox>
                )}
              </Form.Item>
              <Form.Item colon={false}style={formItemstyling}
              label={<span style={labelstyling}>Group Policy:</span>}>
                {getFieldDecorator('policyGroupPolicy', {
                  initialValue: false ,
                  valuePropName: 'checked'
                  })(
                  <Checkbox>
                  </Checkbox>
                )}
              </Form.Item>
            </Col>
          </Row>
          <Row name = "patientForm" style={rowBlockstyling}>
            <Col {...formItemFirstColstyling}>
              <Form.Item colon={false}style={formItemstyling}
              label={<span style={labelstyling}>Patient First Name:</span>}>
                {getFieldDecorator('patientFirstName', {
                  rules: [],
                })(
                  <Input
                  />,
                )}
              </Form.Item>
              <Form.Item colon={false}style={formItemstyling} label={<span style={labelstyling}>Patient Last Name:</span>}>
                {getFieldDecorator('patientLastName', {
                  rules: [],
                })(
                  <Input
                  />,
                )}
              </Form.Item>
            </Col>
            <Col {...formItemSecondColstyling}>
              <Form.Item colon={false}style={formItemstyling} label={<span style={labelstyling}>Patient DOB:</span>}>
                {getFieldDecorator('patientDob', {
                  rules: [],
                })(
                  <DatePicker style={{width:'100%'}}/>
                )}
              </Form.Item>
            </Col>
            <Col {...formItemThirdColstyling}>
              <Form.Item colon={false}style={formItemstyling} label={<span style={labelstyling}>Patient Age:</span>}>
                {getFieldDecorator('patientAge', {
                  rules: [],
                })(
                  <InputNumber/>
                )}
              </Form.Item>
            </Col>
          </Row>
          <Row name = "contactForm" style={rowBlockstyling}>
            <Col {...formItemFirstColstyling}>
              <Form.Item colon={false}style={formItemstyling} label={<span style={labelstyling}>Contact First Name:</span>}>
                {getFieldDecorator('contactFirstName', {
                  rules: [],
                })(
                  <Input
                  />,
                )}
              </Form.Item>
              <Form.Item colon={false} style={formItemstyling} label={<span style={labelstyling}>Contact Last Name:</span>}>
                {getFieldDecorator('contactLastName', {
                  rules: [],
                })(
                  <Input
                  />,
                )}
              </Form.Item>
              <Form.Item colon={false}style={formItemstyling} label={<span style={labelstyling}>Contact Email:</span>}>
                {getFieldDecorator('contactEmail', {
                  rules: [],
                })(
                  <Input
                  />,
                )}
              </Form.Item>
              <Form.Item colon={false}style={formItemstyling} label={<span style={labelstyling}>Contact Home Address:</span>}>
                {getFieldDecorator('contactHomeAddress', {
                  rules: [],
                })(
                  <Input.TextArea rows={4}
                  />,
                )}
              </Form.Item>
            </Col>
            <Col {...formItemSecondColstyling}>
              <Row>
                <Col span={5}>
                  <Form.Item colon={false}style={formItemstyling} label={<span style={labelstyling}>Contact Phone Number:</span>}>
                    {getFieldDecorator('contactPhoneNumberCountryCode', {
                      rules: [],
                    })(
                          <Input placeholder="+000" />

                    )}
                  </Form.Item>
                </Col>
                <Col span={19}>
                  <Form.Item colon={false}style={formItemstyling} label={<span style={labelstyling}></span>}>
                    {getFieldDecorator('contactPhoneNumber', {
                      rules: [],
                    })(

                          <Input placeholder="000000000000" />
                    )}
                  </Form.Item>
                </Col>
              </Row>
              <Form.Item colon={false}style={formItemstyling} label={<span style={labelstyling}>Relationship to Patient:</span>}>
                {getFieldDecorator('relationshipToPatient', {
                  rules: [],
                })(
                  <Input />
                )}
              </Form.Item>
            </Col>
          </Row>
          <Row name = "AccountForm" style={rowBlockstyling}>
            <Col {...formItemFirstColstyling}>
              <Form.Item colon={false}style={formItemstyling} label={<span style={labelstyling}>Account Holder Name:</span>}>
                {getFieldDecorator('accountHoldersName', {
                  rules: [],
                })(
                  <Input
                  />,
                )}
              </Form.Item>
              <Form.Item colon={false}style={formItemstyling} label={<span style={labelstyling}>Bank Name:</span>}>
                {getFieldDecorator('bankName', {
                  rules: [],
                })(
                  <Input
                  />,
                )}
              </Form.Item>
              <Form.Item colon={false}style={formItemstyling} label={<span style={labelstyling}>Bank Address:</span>}>
                {getFieldDecorator('bankAddress', {
                  rules: [],
                })(
                  <Input.TextArea rows={6}
                  />,
                )}
              </Form.Item>
            </Col>
            <Col {...formItemSecondColstyling}>
              <Form.Item colon={false}style={formItemstyling} label={<span style={labelstyling}>Reimbusement Currency:</span>}>
                {getFieldDecorator('reimbusementCurrency', {
                  rules: [
                    {
                      required: true,
                      message: 'Please input Reimbusement Currency',
                    },
                  ],
                })(
                  <Select style={{ width: '100%' }}  placeholder='Auto populate or select...' showSearch filterOption={(input, option) => {
                    return option.props.children[0].toLowerCase().indexOf(input.toLowerCase()) >= 0 ||option.props.children[2].toLowerCase().indexOf(input.toLowerCase()) >= 0
                  }}>
                    {currencyData.map(currency => (
                      <Option value={currency.value} key = {currency.value}>{currency.label} - {currency.value}</Option>
                    ))}
                  </Select>
                )}
              </Form.Item>
              <Form.Item colon={false}style={formItemstyling} label={<span style={labelstyling}>Bank Account Number:</span>}>
                {getFieldDecorator('bankAccountNumber', {
                  rules: [],
                })(
                  <Input placeholder='00000000000000'/>
                )}
              </Form.Item>
              <Form.Item colon={false}style={formItemstyling} label={<span style={labelstyling}>IBAN Code/Sort Code:</span>}>
                {getFieldDecorator('ibanCodeSortCode', {
                  rules: [],
                })(
                  <Input placeholder='00000000000000'/>
                )}
              </Form.Item>
              <Form.Item colon={false}style={formItemstyling} label={<span style={labelstyling}>SWIFT:</span>}>
                {getFieldDecorator('swift', {
                  rules: [],
                })(
                  <Input placeholder='00000000'/>
                )}
              </Form.Item>
            </Col>
          </Row>

          <Row name = "causeForm" style={rowBlockstyling}>
            <Col md={18} sm={22} xs={22}>
              <Form.Item colon={false} style={formItemstyling} label={<span style={labelstyling}>Cause:</span>}>
                {getFieldDecorator('cause', {
                  rules: [],
                })(
                  <Input.TextArea rows={4}
                  />,
                )}
              </Form.Item>
            </Col>
            <Col {...formItemThirdColstyling}>
              <Form.Item colon={false} style={formItemstyling} label={<span style={labelstyling}>GOP:</span>}>
                {getFieldDecorator('gop', {
                  initialValue: false ,
                  valuePropName: 'checked'
                  })(
                  <Checkbox>
                  </Checkbox>
                )}
              </Form.Item>
            </Col>
          </Row>
          <Form.Item>
            <Button type="primary" htmlType="submit">
              Save Claim
            </Button>
          </Form.Item>
          <Row name = "Divider" style={rowBlockstyling}>
            <svg width={948} height={1}>
              <path fill="none" stroke="#fff" d="M0 .5h948" />
            </svg>
          </Row>

        </Form>

        {this.props.claim.claimData.id?
        <>
          <Button type="primary" onClick={this.showModal} style={rowBlockstyling}>
            New Visit
          </Button>
          <CollectionCreateForm
            wrappedComponentRef={this.saveFormRef}
            visible={this.state.visible}
            onCancel={this.handleCancel}
            onCreate={this.handleCreate}
            visit={this.state.visit}
            onRef={ref => (this.child = ref)}
          />
          <Table columns={columns} dataSource={this.props.claim.claimData.ClaimInfoVisits} rowKey="id" style={rowBlockstyling}/>
          <Row style={{paddingTop:50, paddingBottom:20}} type="flex" justify="end">
            <Col pull={3}>
              <Button type="link" htmlType="submit" onClick={() => {

                this.props.submitClaimInfo({id:this.props.claim.claimID})
              }}>
                  <Icon component={SvgButtonComponent} />
              </Button>
            </Col>
          </Row>
          <Row>
            <svg width={949} height={5}>
              <path
                fill="none"
                stroke="#f17d27"
                strokeWidth={5}
                d="M0 2.5h949"
              />
            </svg>
          </Row>
        </>
        :null}
      </Col>
    );
  }
}
//https://github.com/ant-design/ant-design/issues/2985
const WrappedPreviewWithLayoutForm = Form.create({
  name: 'REGISTER_Claims_Form',
  mapPropsToFields(props) {
    return {
      policyFirstName: Form.createFormField({
        ...props.policyFirstName,
        value: props.claim.claimData.policyFirstName,
      }),
      policyLastName: Form.createFormField({
        ...props.policyLastName,
        value: props.claim.claimData.policyLastName,
      }),
      policyEmail: Form.createFormField({
        ...props.policyEmail,
        value: props.claim.claimData.policyEmail,
      }),
      policyNumber: Form.createFormField({
        ...props.policyNumber,
        value: props.claim.claimData.policyNumber,
      }),
      policyType: Form.createFormField({
        ...props.policyType,
        value: props.claim.claimData.policyType,
      }),
      policyVip: Form.createFormField({
        ...props.policyVip,
        value: props.claim.claimData.policyVip,
      }),
      policyGroupPolicy: Form.createFormField({
        ...props.policyGroupPolicy,
        value: props.claim.claimData.policyGroupPolicy,
      }),
      patientFirstName: Form.createFormField({
        ...props.patientFirstName,
        value: props.claim.claimData.patientFirstName,
      }),
      patientLastName: Form.createFormField({
        ...props.patientLastName,
        value: props.claim.claimData.patientLastName,
      }),
      patientDob: Form.createFormField({
        ...props.patientDob,
        value: props.claim.claimData.patientDob?moment(props.claim.claimData.patientDob):null,
      }),
      patientAge: Form.createFormField({
        ...props.patientAge,
        value: props.claim.claimData.patientAge,
      }),
      contactFirstName: Form.createFormField({
        ...props.contactFirstName,
        value: props.claim.claimData.contactFirstName,
      }),
      contactLastName: Form.createFormField({
        ...props.contactLastName,
        value: props.claim.claimData.contactLastName,
      }),
      contactEmail: Form.createFormField({
        ...props.contactEmail,
        value: props.claim.claimData.contactEmail,
      }),
      contactHomeAddress: Form.createFormField({
        ...props.contactHomeAddress,
        value: props.claim.claimData.contactHomeAddress,
      }),
      contactPhoneNumberCountryCode: Form.createFormField({
        ...props.contactPhoneNumberCountryCode,
        value: props.claim.claimData.contactPhoneNumberCountryCode,
      }),
      contactPhoneNumber: Form.createFormField({
        ...props.contactPhoneNumber,
        value: props.claim.claimData.contactPhoneNumber,
      }),
      relationshipToPatient: Form.createFormField({
        ...props.relationshipToPatient,
        value: props.claim.claimData.relationshipToPatient,
      }),
      accountHoldersName: Form.createFormField({
        ...props.accountHoldersName,
        value: props.claim.claimData.accountHoldersName,
      }),
      bankName: Form.createFormField({
        ...props.bankName,
        value: props.claim.claimData.bankName,
      }),
      bankAddress: Form.createFormField({
        ...props.bankAddress,
        value: props.claim.claimData.bankAddress,
      }),
      reimbusementCurrency: Form.createFormField({
        ...props.reimbusementCurrency,
        value: props.claim.claimData.reimbusementCurrency,
      }),
      bankAccountNumber: Form.createFormField({
        ...props.bankAccountNumber,
        value: props.claim.claimData.bankAccountNumber,
      }),
      ibanCodeSortCode: Form.createFormField({
        ...props.ibanCodeSortCode,
        value: props.claim.claimData.ibanCodeSortCode,
      }),
      swift: Form.createFormField({
        ...props.swift,
        value: props.claim.claimData.swift,
      }),
      cause: Form.createFormField({
        ...props.cause,
        value: props.claim.claimData.cause,
      }),
      gop: Form.createFormField({
        ...props.gop,
        value: props.claim.claimData.gop,
      }),
    };
  },
})(PreviewWithLayout);

export default connect(
  state => {
    //console.log('state', state)
    return {idToken: state.Auth.idToken, claim: state.Claim, page:state.router.location.pathname.split('/')[2]}
  },
  action
)(WrappedPreviewWithLayoutForm);
