import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Row, Col, Form, Button, Icon, Empty  } from 'antd';
import actions from '../../redux/claim/actions';
import FormBillingInfoDetails from './billingInfoDetails';
import {sum} from 'd3-array';
import BenefitsLabelData from './BenefitLabelDataInit';
import SuperFetch from '../../helpers/superFetch';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
import base64url from 'base64url';
const SvgButtonComponent = props => (
  <svg width={145} height={52}>
    <path
      d="M3.494 0h115.811l23.11 27.246L119.305 52H3.494C1.564 52 0 48.866 0 45V7c0-3.866 1.564-7 3.494-7z"
      fill="#f17d27"
    />
    <text
      transform="translate(61.415 33)"
      fill="#fff"
      fontSize={20}
      fontFamily="SegoeUI-Semibold, Segoe UI"
      fontWeight={600}
      letterSpacing=".02em"
    >
      <tspan x={-37.191} y={0}>
        {'SUBMIT'}
      </tspan>
    </text>
  </svg>
);

const SvgComponent = props => (
  <svg width={665} height={76} {...props}>
    <defs>
      <linearGradient
        id="prefix__a"
        x1={0.5}
        y1={1}
        x2={0.5}
        gradientUnits="objectBoundingBox"
      >
        <stop offset={0} stopColor="#25325a" />
        <stop offset={1} stopColor="#5b6fa3" />
      </linearGradient>
    </defs>
    <path
      transform="translate(0 33)"
      fill="url(#prefix__a)"
      d="M0 0h665v43H0z"
    />
    <text
      transform="translate(253 62)"
      fill="#fff"
      fontSize={20}
      fontFamily="SegoeUI-Semibold, Segoe UI"
      fontWeight={600}
    >
      <tspan x={-215.723} y={0}>
        {'Total Amounts Claimed:'}
      </tspan>
    </text>
    <text
      data-name="0,000,000.00"
      transform="translate(555 62)"
      fill="#fff"
      fontSize={20}
      fontFamily="SegoeUI-Bold, Segoe UI"
      fontWeight={700}
    >
      <tspan x={0} y={0}>
        {props.total.toFixed(2)}
      </tspan>
    </text>

    <text
      transform="translate(593 23)"
      fill="#fff"
      fontSize={20}
      fontFamily="SegoeUI-Semibold, Segoe UI"
      fontWeight={600}
    >
      <tspan x={-19.648} y={0}>
        {'USD'}
      </tspan>
    </text>
  </svg>
)

class BillingInfoForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      //visitsData:[],
      BenefitsLabel: new BenefitsLabelData().getAll(),
      totalUSD:0,
      claimInfo:{}
    }
    this.clickChild = {};
  }

  componentDidMount() {

    if (this.props.claim.claimID){
      SuperFetch.get('claim/claimById',{id:base64url(this.props.claim.claimID)})
      .then(response => {

        if(response.status === 200) {

          this.setState({
            claimInfo:response.data,
            totalUSD: sum(response.data.ClaimInfoVisits, visit => sum(visit.BillingInfos, billingInfo => billingInfo.value )/visit.billingRate)
          });
        } else {
          notification('error', "Something wrong in retrieving data" );
        }
      })
      .catch(e => console.log(e))
    }

  }

  async updateTotal() {
    const tasks = Object.values(this.clickChild).map(fn => {
      return fn();
    });
    var total = await Promise.all(tasks)
    .then(function(visits) {
      console.log(visits)
      return sum(visits, visit => sum(visit.BillingInfos, billingInfo => billingInfo.value )/visit.billingRate)
    })

    this.setState({totalUSD:total});
  }

  render() {
    //https://ant.design/components/form/?locale=en-US#components-form-demo-form-in-modal
    //Store Form Data into Upper Component
    return (
      <Col md={24} sm={18} xs={18}>
        { this.state.claimInfo.ClaimInfoVisits?
          (
              this.state.claimInfo.ClaimInfoVisits.length > 0 ?
              this.state.claimInfo.ClaimInfoVisits.map(visit => (
              <FormBillingInfoDetails
                key={visit.id}
                visit={visit}
                BenefitsLabel={this.state.BenefitsLabel}
                updateTotal = {this.updateTotal.bind(this)}
                setClick={click => {
                  this.clickChild[visit.id] = click;
                }}
                />))
              : <Empty description={<span>No Visit entered</span>}/>
          )
          : <Empty description={<span>No Visit entered</span>}/>
        }

          <Row style={{paddingTop:50, paddingBottom:20}} type="flex" justify="end">
            <SvgComponent total={this.state.totalUSD}/>

          </Row>
          <Row style={{paddingTop:50, paddingBottom:20}} type="flex" justify="end">
            <Col pull={3}>
              <Button type="link" htmlType="submit" claiminfo={this.state.claimInfo}
              onClick={() => {
                this.props.setTabIndex('DocumentsButton')

              }}>
                  <Icon component={SvgButtonComponent} />
              </Button>
            </Col>
          </Row>
          <Row>
            <svg width={949} height={5}>
              <path
                fill="none"
                stroke="#f17d27"
                strokeWidth={5}
                d="M0 2.5h949"
              />
            </svg>
          </Row>
      </Col>
    );
  }
}

const WrappedBillingInfoForm = Form.create({ name: 'BillingInfoForm' })(
  BillingInfoForm
);

export default connect(
  state => {

    return { claim: state.Claim};
  },
  actions
)(WrappedBillingInfoForm);
