import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Form, Row, Col, Typography, Table } from 'antd';
import Information from '../../Information'
import moment from 'moment';
import {sum} from 'd3-array';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
import SuperFetch from '../../../helpers/superFetch';
class BasicInfos extends Component {
  constructor(props) {
    super(props);
    this.state = {
      patientClaims: [],
    }
  }

  componentDidMount() {

    if (this.props.params){
      SuperFetch.get('evaluate/patientClaims',this.props.params)
      .then(response => {

        if(response.status === 200) {
          this.setState({patientClaims:response.data});
          //notification('succuess', "done" );
        } else {
          notification('error', "Something wrong in retrieving Patient Previous Claims" );
        }
      })
      .catch(e => console.log(e))
    } else {
      notification('error', "No soch claim" );
    }

  }
  render() {
    const columns = [
      {
        title: 'Date',
        dataIndex: 'createdAt',
        key: 'createdAt',
        render: (text, record) => {
          return moment(text).format('L')
        },
      },
      {
        title: 'UCN',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Cause',
        dataIndex: 'cause',
        key: 'cause',
      },
      {
        title: 'Approved Amount',
        render: (text, record) =>{
          return sum(record.ClaimInfoVisits, visit => sum(visit.BillingInfos, billing => billing.value) / visit.billingRate).toFixed(2)
        },
      },
    ];

    return (
      <Row>
        <Col span={6}>
          <Row>
            <Typography.Title level={4}>This Policy:</Typography.Title>
          </Row>
          <Row>
            <Col span={12}>
              <Information title="Approved YTD" content="?" />
              <Information title="Policy Type" content={this.props.claim.policyType} />
              <Information title="Insured" content="?" />
              <Information title="Previous" content="?" />
            </Col>
            <Col span={12}>
              <Information title="Gross Premium" content={this.props.claim.policyType} />
              <Information title="Start Date" content="?" />
              <Information title="Payment Frequency" content="?" />
            </Col>
          </Row>
        </Col>
        <Col span={5}>

            <Col span={12}>
            <Typography.Title level={4}>This Claim:</Typography.Title>
              <Information title="Registered" content={moment(this.props.claim.createdAt).format('L')} />
              <Information title="Cause" content={this.props.claim.cause} />
              <Information title="claimed amount" content={(this.props.claim.ClaimInfoVisits?sum(this.props.claim.ClaimInfoVisits, visit => sum(visit.BillingInfos, billing => billing.value) / visit.billingRate).toFixed(2):
              'unknown') + ' USD'} />
            </Col>
            <Col span={12}>
              <Information title="VIP" content={this.props.claim.policyVip?'Yes':'No'} />
              <Information title="Group" content={this.props.claim.policyGroupPolicy?'Yes':'No'} />
              <Information title="GOP" content={this.props.claim.gop?'Yes':'No'} />
              <Information title="Fraud" content={this.props.claim.fraud?'Yes':'No'} />
            </Col>

        </Col>
        <Col span={5}>
          <Row>
            <Typography.Title level={4}>This Patient:</Typography.Title>
          </Row>
            <Information title="Approved YTD" content={this.props.claim.approvedYTD} />
            <Information title="Age" content={this.props.claim.age} />
            <Information title="Consideration YTD" content={this.props.claim.considerationYTD} />
            <Information title="Gender" content={this.props.claim.gender} />
        </Col>
        <Col span={8}>
          <Row>
            <Typography.Title level={4}>Patient Previous Claims:</Typography.Title>
          </Row>
          <Table columns={columns} dataSource={this.state.patientClaims} rowKey="id" size="small"/>
        </Col>
      </Row>
    );
  }
}


const WrappedBasicInfosForm = Form.create({ name: 'BillingInfoForm' })(BasicInfos);

export default connect(
  state => ({}),
  {}
)(WrappedBasicInfosForm);
