import React, {Component} from 'react';
import { connect } from 'react-redux';
import {Row, Col, Divider, Button, Drawer, Typography } from 'antd';
import Comments from '../Comments'

class HeaderItem extends Component {
  constructor(props) {
    super(props);

    this.state = {
      visible: false,
    }
  }


  showDrawer = () => {
    this.setState({
      visible: true,
    });
  };

  onClose = () => {
    this.setState({
      visible: false,
    });
  };
  render() {
    return  (
      <>
        <Row type="flex" justify="end" align="middle" offset={1}>
          <Col md={4} sm={11} xs={11}>
            <p style={{fontFamily:'SegoeUI, Segoe UI',fontSize:14,color:'white',marginBottom: '0'}}>{'Patient Name: '}</p>
            <p style={{fontFamily:'SegoeUI, Segoe UI',fontSize:14,color:'white',marginBottom: '0'}}>{this.props.claimData.patientFirstName||this.props.claimData.patientFirstName?this.props.claimData.patientFirstName+ ' '+ this.props.claimData.patientLastName:''}</p>
          </Col >
          <Col md={4} sm={11} xs={11} >
            <p style={{fontFamily:'SegoeUI, Segoe UI',fontWeight:'bold',fontSize:14,color:'#f17d27',marginBottom: '0'}}>{'UCN:'} </p>
            <p style={{fontFamily:'SegoeUI, Segoe UI',fontSize:14,color:'white',marginBottom: '0'}}>{this.props.claimData.id}</p>
          </Col>
        </Row>
        <Row type="flex" justify="end">
          <Col md={8} sm={22} xs={22}>
            <Divider />
          </Col>
        </Row>
        <Row type="flex" justify="end">
          <Button type="primary" onClick={this.showDrawer}>
            Comments
          </Button>
          <Drawer
            title="Comments"
            placement="right"
            closable={false}
            onClose={this.onClose}
            visible={this.state.visible}
            width={'50%'}
          >
            <Comments/>
          </Drawer>
        </Row>
        <Row>
          <Col span={12} >
            <Typography.Title>{this.props.title}</Typography.Title>
          </Col>
        </Row>
      </>
    );
  }
}

export default connect(

  state => ({claimData:state.Claim.claimData}),
  {}
)(HeaderItem);
