import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Table } from 'antd';
import _ from 'lodash';
import {
  EditableFormRow,
  EditableCell
} from '@isomorphic/shared/isomorphic/components/Tables/CustomizedCellsNumber';
import {sum} from 'd3-array';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
import SuperFetch from '../../../helpers/superFetch';
import TableStyleWrapper from './tableSub.style';
class TableSub extends Component {

  constructor(props) {
    super(props);
    var billingData =_.chain(props.visit.BillingInfos).groupBy("BenefitCategory.id").map((v, i) => {
      var newArray = v.map(billing => {
        return {
          id:billing.id,
          name:billing.BenefitSubCategory.name,
          claimedBC: Number(billing.value).toFixed(2),
          approvedBC: Number(billing.approved).toFixed(2),
        }
      })
      return {
        id: 'cat'+v[0].BenefitCategory.id,
        name: v[0].BenefitCategory.name,
        claimedUSD: sum(newArray, child => child.claimedUSD),
        claimedBC: sum(newArray, child => child.claimedBC),
        children: newArray
      }
    }).value();
    this.state = {data: billingData}
  }
  handleSave = row => {
    SuperFetch.put('claim/billingInfo',{id:row.id,approved:row.approvedBC})
    .then(response => {

      if(response.status === 200) {
        //I don't know why this is still needed **************************************
        //but without it, the table state won't update
        this.state.data.map(cat => {
          const index = cat.children.findIndex(child => child.id === row.id)
          if(index>-1)
            cat.children.splice(index,1,row)
          return cat
        })
        // as long sa one partial
        this.props.getTotal()

      } else {
        notification('error', "Something wrong in Basic data" );
      }
    })
    .catch(e => console.log(e))



  };

  render() {
    const columns = [
      {
        title: 'Name',
        dataIndex: 'name',
        key: 'id',
      },
      {
        title: 'Amount Claimed in B/C ('+ this.props.visit.billingCurrency +')',
        dataIndex: 'claimedBC',
        render: (text, record) =>{
          if (record.hasOwnProperty('children'))
            return false
          return text
        },
      },
      {
        title: 'Amount Claimed in USD',
        render: (text, record) =>{
          if (record.hasOwnProperty('children'))
            return false
          return (record.claimedBC/this.props.visit.billingRate).toFixed(2)
        },
      },
      {
        title: 'Amount Claimed in R/C ('+ this.props.reimbusementCurrency +')',
        render: (text, record) =>{
          if (record.hasOwnProperty('children'))
            return false
          return ((record.claimedBC/this.props.visit.billingRate) * this.props.RCExchangeRate).toFixed(2)
        },
      },
      {
        title: 'Amount Approved in B/C',
        dataIndex: 'approvedBC',
        onCell: record => {
          if (record.hasOwnProperty('children'))
            return false
          return {
            record,
            editable: this.props.page === 'decision' || record.hasOwnProperty('children') ?false:true,
            dataIndex: 'approvedBC',
            handleSave: this.handleSave,
          }
        },
      },
      {
        title: 'Amount Approved in USD',
        render: (text, record) =>{
          if (record.hasOwnProperty('children'))
            return false
          const result = (record.approvedBC/this.props.visit.billingRate).toFixed(2)

          return isNaN(result)?0:result
        },
      },
      {
        title: 'Amount Approved in R/C ('+ this.props.reimbusementCurrency +')',
        render: (text, record) =>{
          if (record.hasOwnProperty('children'))
            return false
          const result = ((record.approvedBC/this.props.visit.billingRate)* this.props.RCExchangeRate).toFixed(2)
          return isNaN(result)?0:result
        },
      },
    ];

    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell,
      },
    };
    return (
      <TableStyleWrapper>
        <Table  columns={columns} components={components}
         rowKey={'id'}  dataSource={this.state.data}
        />
      </TableStyleWrapper>
    );
  }
}

export default connect(
  state => ({}),
  {}
)(TableSub);
