class ScrumBoard extends Component {
  render() {
    return (
      <>
        <Switch>
          <Route exact path={`${this.props.match.path}`} component={EvaluateList}/>
          <Route path={`${this.props.match.path}/project/:id`} component={EvaluateDetail} />
        </Switch>
      </>
    );
  }
}

export default connect(
  state => {
    return state.Claim;
  },
  {}
)(ScrumBoard);
