import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Form, Table, Row, Col, Input} from 'antd';
import actions from '../../redux/claim/actions';
import {sum} from 'd3-array';
import {
  EditableFormRow,
  EditableCell
} from '@isomorphic/shared/isomorphic/components/Tables/CustomizedCellsNumber';



class BillingInfoDetailsSubForm extends Component {
  constructor(props) {
    super(props);


    this.columns = [
      {
        title: 'Name',
        dataIndex: 'name',
        key: 'name',
        width: '45%',
        render: (text, record) =>{

          return  (record.id === 39? <Input
            ref={node => (this.inputOther = node)} onPressEnter={this.saveOther} onBlur={this.saveOther}
          /> :text)
        },
      },
      {
        title: 'BillingInfo',
        dataIndex: 'BillingInfo',
        editable: true,
        width: '25%',
      },
      {
        title: 'USD',
        dataIndex: 'BillingInfo',
        key: 'USD',
        render: (text, record) =>{
          return  (this.props.currencyRate?<p>{(Number(text)/this.props.currencyRate).toFixed(2)}</p>:0)
        },
        width: '25%',
      },
    ];


    this.state = {
      billingInfosSub:this.props.billingInfosSub
    }

    this.getAlert = this.getAlert.bind(this);
  }

  componentDidMount() {
    this.props.setClick(this.getAlert);
  }
  saveOther = e => {
    //this.props.uploadBillingInfoOtherName({visitId:this.props.visit.id, value: this.inputOther.state.value})

  };
  handleSave = row => {

    var newBillingInfosSub = this.state.billingInfosSub
    var index = newBillingInfosSub.findIndex(x => x.billingSubCat === row.id)
    row.BillingInfo=row.BillingInfo === null? 0:row.BillingInfo
    if (index > -1) {
      newBillingInfosSub[index].value = row.BillingInfo
    } else {
      newBillingInfosSub.push({visitId:this.props.visitId,billingCat:this.props.categoryId,billingSubCat:row.id,value:row.BillingInfo})
    }
    console.log(newBillingInfosSub)
    this.setState({billingInfosSub:newBillingInfosSub})
    this.props.updateTotal()
    //row.BillingInfo = row.BillingInfo===null?0:row.BillingInfo

    this.props.updateBillingInfo({visitId:this.props.visitId,billingCat:this.props.categoryId,billingSubCat:row.id,value:row.BillingInfo})
  };

  getAlert() {
    return new Promise(
      (resolve, reject) => {

        resolve(this.state.billingInfosSub);
      }
    )
  }


  render() {

    var tableData = this.props.BenefitSubCategories.map(obj => {
      obj.BillingInfo = this.state.billingInfosSub.find(bill => bill.billingSubCat === obj.id)?this.state.billingInfosSub.find(bill => bill.billingSubCat === obj.id).value:null
      return obj
    })


    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell,
      },
    };
    const columns = this.columns.map(col => {
      if (!col.editable) {
        return col;
      }
      return {
        ...col,
        onCell: record => ({
          record,
          editable: col.editable,
          dataIndex: col.dataIndex,
          title: col.title,
          handleSave: this.handleSave,
        }),
      };
    });

    return (
      <div>
        <Table components={components}
        dataSource={tableData}
        columns={columns}
        rowKey="id"
        showHeader={false}
        footer={() => (
          <Row type="flex" justify="end">
            <Col span={6}>
              {'Amounts Cliamed: '}
            </Col>
            <Col span={6}>
              {sum(this.state.billingInfosSub, d => d.value).toFixed(2)}
            </Col>
            <Col span={6}>
              {this.props.currency?(sum(this.state.billingInfosSub, d => d.value)/this.props.currencyRate).toFixed(2):0}
            </Col>
          </Row>
        )}/>
      </div>
    );
  }
}


const WrappedBillingInfoDetailsSubForm = Form.create({ name: 'BillingInfoDetailsSubForm' })(BillingInfoDetailsSubForm);

export default connect(

  state => {
    return {}
  },
  actions
)(WrappedBillingInfoDetailsSubForm);
