import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Form, Switch, Modal, Button, Row, Col, Input, Typography} from 'antd';
import BankDocs from './BankDocs';
import SuperFetch from '../../../helpers/superFetch';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
import jwtDecode from 'jwt-decode';
import BankInfoStyleWrapper from './bankInfo.style';
class Assessment extends Component {
  constructor(props) {
    super(props);

    this.state = {
      visible: false,
      evaluate:''
    }
  }
  showModal = () => {
    this.setState({
      visible: true,
    });
  };
  handleOk = e => {
    this.setState({
      visible: false,
    });
  };

  handleCancel = e => {
    this.setState({
      visible: false,
    });
  };

  componentDidMount() {

    if (this.props.page === 'decision'){

      SuperFetch.get('evaluate/assessment',this.props.params)
      .then(response => {

        if(response.status === 200) {

          this.props.form.setFieldsValue(response.data.assessment);
          if(response.data.evaluate)
            this.setState({evaluate:response.data.evaluate.message});
          //notification('succuess', "done" );
        } else {
          notification('error', "Something wrong in Basic data" );
        }
      })
      .catch(e => console.log(e))

    } else if (this.props.page === 'evaluate') {
      return
    }
    else {
      notification('error', "No such claim" );
    }

  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll(async (err, data) => {

      if (!err) {

        const profile = jwtDecode(this.props.idToken);
        data.claimInfoId = this.props.claim.id
        data.createdBy = profile.id

        if(this.props.page === 'evaluate') {
          SuperFetch.post('evaluate/assessment',{data})
          .then(response => {
            if(response.status === 200) {
              this.props.history.goBack()
            } else {
              notification('error', "Something wrong in retrieving data" );
            }
          })
          .catch(e => console.log(e))
        }

      }
    })
  };

  disagree = e => {
    this.props.form.validateFieldsAndScroll(async (err, data) => {
      if (!err) {
        var decision = {}
        const profile = jwtDecode(this.props.idToken);
        decision.claimInfoId = this.props.claim.id
        decision.createdBy = profile.id
        decision.group = 'decision'
        decision.message = data.decision
        var status ={status:'re-pd'}
        console.log(this.props.flag)
        SuperFetch.post('evaluate/decision',{decision,status})
        .then(response => {
          if(response.status === 200) {
            this.props.history.goBack()
          } else {
            notification('error', "Something wrong in retrieving data" );
          }
        })
        .catch(e => console.log(e))
      }
    })
  }
  approve = e => {
    this.props.form.validateFieldsAndScroll(async (err, data) => {
      if (!err) {
        var decision = {}
        const profile = jwtDecode(this.props.idToken);
        decision.claimInfoId = this.props.claim.id
        decision.createdBy = profile.id
        decision.group = 'decision'
        decision.message = data.decision

        var status ={}

        switch (this.props.flag) {
          case 'decline':
            status.status ='cd';
            break;
          case 'approve':
            status.status ='ca';
            break;
          case 'patial':
            status.status ='cp';
            break;
          case 'condiseration':
            status.status ='cc';
            break;
          default:
            status.status ='cd';
            break;
        }
        SuperFetch.post('evaluate/decision',{decision,status})
        .then(response => {
          if(response.status === 200) {
            this.props.history.goBack()
          } else {
            notification('error', "Something wrong in retrieving data" );
          }
        })
        .catch(e => console.log(e))
      }
    })
  }

  render() {

    var buttonColor =''
    var buttonText = ''
    switch (this.props.flag) {
      case 'decline':
        buttonColor = '#DB2828';
        buttonText = 'DECLINE';
        break;
      case 'approve':
        buttonColor = '#1BA12C';
        buttonText = 'APPROVE';
        break;
      case 'patial':
        buttonColor = '#EEA220';
        buttonText = 'PARTIALLY PAY';
        break;
      case 'condiseration':
        buttonColor = '#DB2828';
        buttonText = 'CONSIDERATION';
        break;
      default:
        buttonColor = '#DB2828';
        buttonText = 'DECLINE';
        break;
    }

    var disabled = this.props.page === 'evaluate'? false:true
    const { getFieldDecorator } = this.props.form;
    let RationaleDecision;
    switch(this.props.page) {
      case 'evaluate':
          RationaleDecision = (<Form.Item>
          {getFieldDecorator('rationale', {
            rules: [
              {
                required: true,
                message: 'Please input your decision',
              },
            ],
          })(<Input.TextArea rows={3} placeholder="Reason for decision..." />)}
        </Form.Item>);
          break;
      case 'decision':
          RationaleDecision = (
            <>
              <Col span={9}>
                <Row>
                  <Typography.Title level={4}>Rationale:</Typography.Title>
                </Row>
                <span>{this.state.evaluate}</span>
              </Col>
              <Col span={9}>
                <Row>
                  <Typography.Title level={4}>Decision:</Typography.Title>
                </Row>
                <Form.Item>
                {getFieldDecorator('decision', {
                    rules: [
                      {
                        required: true,
                        message: 'Please input your decision',
                      },
                    ],
                  })(<Input.TextArea rows={3} placeholder="Reason for decision..." />)}
                </Form.Item>
              </Col>
            </>
          );
          break;
      default:
          RationaleDecision = "I don't know which page it is";
          break;
    }
    return (
      <>
        <Form layout="inline" onSubmit={this.handleSubmit} colon={false}>
          <Col span={12}>
            <Row>
              <Typography.Title level={4}>This Policy:</Typography.Title>
            </Row>
            <Col span={10}>
              <Form.Item label={<span>Is this claim for a Pre-Existing condition?</span>}>
                {getFieldDecorator('forPreExisting', { valuePropName: 'checked' })(<Switch disabled={disabled}/>)}
              </Form.Item>
              <Form.Item label={<span>Does it realte to a Pre-Exsting condition?</span>}>
                {getFieldDecorator('relatePreExisting', { valuePropName: 'checked' })(<Switch disabled={disabled}/>)}
              </Form.Item>
              <Form.Item label={<span>Do any other Policy Exclusions apply?</span>}>
                {getFieldDecorator('otherExclusion', { valuePropName: 'checked' })(<Switch disabled={disabled}/>)}
              </Form.Item>
            </Col>
            <Col span={12} offset={2}>
              <Form.Item label={<span>Are all the charges reasonable and customary?</span>}>
                {getFieldDecorator('reasonable', { valuePropName: 'checked' })(<Switch disabled={disabled}/>)}
              </Form.Item>
              <Form.Item label={<span>Does this claim adhere to the Policy Workding?</span>}>
                {getFieldDecorator('adhere', { valuePropName: 'checked' })(<Switch disabled={disabled}/>)}
              </Form.Item>
            </Col>
          </Col>
          <Col span={2} offset={1}>
            <Row>
              <Typography.Title level={4}>Documents Bank Details:</Typography.Title>
            </Row>
            <Button type="primary" onClick={this.showModal}>
              FILE
            </Button>
            <BankInfoStyleWrapper>
              <Modal
                width={'70%'}
                visible={this.state.visible}
                onOk={this.handleOk}
                onCancel={this.handleCancel}
                footer={[
                  <Button key="back" onClick={this.handleCancel}>
                    OK
                  </Button>
                ]}
              >
                <BankDocs claim={this.props.claim}/>
              </Modal>
            </BankInfoStyleWrapper>
          </Col>
          <Col span={8} offset={1}>
            {RationaleDecision}
            {this.props.page === 'evaluate'?(
              <Form.Item>
                <Button style={{background: buttonColor}} htmlType="submit">
                  {buttonText}
                </Button>
              </Form.Item>
            ):null}
            {this.props.page === 'decision'?(
              <>
                <Form.Item>
                  <Button style={{background: '#DB2828'}} onClick={this.disagree}>
                    DISAGREE
                  </Button>
                </Form.Item>
                <Form.Item>
                  <Button style={{background: '#1BA12C'}} onClick={this.approve}>
                    APPROVE
                  </Button>
                </Form.Item>
              </>
            ):null}
          </Col>
        </Form>
      </>
    );
  }
}


const WrappedAssessmentForm = Form.create({ name: 'AssessmentForm' })(Assessment);

export default connect(
  state => ({idToken: state.Auth.idToken}),
  {}
)(WrappedAssessmentForm);
