import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { Row, Col, Form, Table, Typography, Divider, Input, Button, Icon} from 'antd';
import actions from '../../../redux/claim/actions';
import moment from 'moment';
import {sum} from 'd3-array';
import Highlighter from 'react-highlight-words';
import base64url from 'base64url';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
import SuperFetch from '../../../helpers/superFetch';
const { Title } = Typography;

class Evaluate extends Component {

  constructor(props) {
    super(props);
    this.state = {
      searchText: '',
      page:this.props.match.path.split('/')[2],
      data:[]
    }
  }

  componentDidMount() {
    var query = {}
    switch(this.state.page) {
      case 'evaluate':
        query = {or: [{status: 'pe'}, {status: 're-pd'}]}
        break;
      case 'decision':
        query.status = 'pd'
        break;
      default:
        break;
    }
    SuperFetch.get('claim/claims',query)
    .then(response => {

      if(response.status === 200) {

        this.setState({data:response.data});
      } else {
        notification('error', "Something wrong in retrieving data" );
      }
    })
    .catch(e => console.log(e))
  }

  getColumnSearchProps = dataIndex => ({
    filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
      <div style={{ padding: 8 }}>
        <Input
          ref={node => {
            this.searchInput = node;
          }}
          placeholder={`Search ${dataIndex}`}
          value={selectedKeys[0]}
          onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
          onPressEnter={() => this.handleSearch(selectedKeys, confirm)}
          style={{ width: 188, marginBottom: 8, display: 'block' }}
        />
        <Button
          type="primary"
          onClick={() => this.handleSearch(selectedKeys, confirm)}
          icon="search"
          size="small"
          style={{ width: 90, marginRight: 8 }}
        >
          Search
        </Button>
        <Button onClick={() => this.handleReset(clearFilters)} type="primary" size="small" style={{ width: 90 }}>
          Reset
        </Button>
      </div>
    ),
    filterIcon: filtered => (
      <Icon type="search" style={{ color: filtered ? '#1890ff' : undefined }} />
    ),
    onFilter: (value, record) =>{
      console.log(record)
      record[dataIndex] = record[dataIndex] === null? '':record[dataIndex]
      return record[dataIndex]
        .toString()
        .toLowerCase()
        .includes(value.toLowerCase())
      },
    onFilterDropdownVisibleChange: visible => {
      if (visible) {
        setTimeout(() => this.searchInput.select());
      }
    },
    render: text => (
      <Highlighter
        highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
        searchWords={[this.state.searchText]}
        autoEscape
        textToHighlight={text?text.toString():''}
      />
    ),
  });

  handleSearch = (selectedKeys, confirm) => {
    console.log(selectedKeys)
    confirm();
    this.setState({ searchText: selectedKeys[0] });
  };

  handleReset = clearFilters => {
    clearFilters();
    this.setState({ searchText: '' });
  };
  render() {

    const rowBlockstyling = {
      marginTop: '5%'
    };
    const columns = [
      {
        title: 'UCN',
        dataIndex: 'id',
        render: (text, record) => {
          return (<Link to={`/dashboard/${this.state.page}/project/${base64url(record.id)}`}>{text}</Link>)
        },
        //...this.getColumnSearchProps('id'),
        sorter: (a, b) => a.id - b.id,
      },
      {
        title: 'Policy Number',
        dataIndex: 'policyNumber',
        ...this.getColumnSearchProps('policyNumber'),
      },
      {
        title: 'Patient First Name',
        dataIndex: 'patientFirstName',
        ...this.getColumnSearchProps('patientFirstName'),
      },
      {
        title: 'Patient Last Name',
        dataIndex: 'patientLastName',
        ...this.getColumnSearchProps('patientLastName'),
      },
      {
        title: 'VIP',
        dataIndex: 'policyVip',
        render: val => (val ? 'Yes' : 'No'),
        filters: [{ text: 'Yes', value: true }, { text: 'No', value: false }],
        onFilter: (value, record) => {
          return record.policyVip === value
        },
      },
      {
        title: 'GOP',
        dataIndex: 'gop',
        render: val => (val ? 'Yes' : 'No'),
        filters: [{ text: 'Yes', value: true }, { text: 'No', value: false }],
        onFilter: (value, record) => {
          return record.gop === value
        },
      },
      {
        title: 'Total(USD)',
        dataIndex: 'total',
        render: (text, record) =>{
          return sum(record.ClaimInfoVisits, visit => sum(visit.BillingInfos, billing => billing.value) / visit.billingRate).toFixed(2)
        },
        sorter: (a, b) =>{
          const constA = sum(a.ClaimInfoVisits, visit => sum(visit.BillingInfos, billing => billing.value) / visit.billingRate).toFixed(2)
          const constB = sum(b.ClaimInfoVisits, visit => sum(visit.BillingInfos, billing => billing.value) / visit.billingRate).toFixed(2)
          return ( constA - constB)
        },

        //https://codesandbox.io/s/926l7qkopp
        // filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
        //   <div style={{ padding: 8 }}>
        //     <Slider range defaultValue={[20, 50]} ref={node => {
        //                 this.searchSlider = node;
        //               }} value={selectedKeys[0]}
        //               onChange={e => {
        //                 console.log(e)
        //                 setSelectedKeys(e)
        //               }}
        //               onPressEnter={() => this.handleSearch(selectedKeys, confirm)}/>
        //     <Button
        //       type="primary"
        //       onClick={() => this.handleSearch(selectedKeys, confirm)}
        //       icon="search"
        //       size="small"
        //       style={{ width: 90, marginRight: 8 }}
        //     >
        //       Search
        //     </Button>
        //     <Button onClick={() => this.handleReset(clearFilters)} type="primary" size="small" style={{ width: 90 }}>
        //       Reset
        //     </Button>
        //   </div>
        // ),

      },
      {
        title: 'Date',
        dataIndex: 'createdAt',
        render: test => (moment(test).format('L')),
        sorter: (a, b) =>{
          return (moment(a.createdAt) - moment(b.createdAt))
        },
      },
    ];
    return (

      <Row>
        <Col span={15} offset={4} >
          <Row style={rowBlockstyling}>
              <Col>
                <Title>{this.state.page==='evaluate'?"Evaluate":'Decide'} Claim</Title>
              </Col>
            <Divider />
          </Row>
          <Row style={rowBlockstyling}>
              <Table columns={columns} dataSource={this.state.data}  style = {{color:'white'}} rowKey="id"/>
          </Row>
        </Col>
      </Row>

  );
  }
}

const WrappedEvaluateForm = Form.create()(Evaluate);

export default connect(
  state => {
    return state.Claim;
  },
  actions
)(WrappedEvaluateForm);
